import React from 'react'
import { Link } from 'react-router-dom'
import classes from './Button.module.css'

const button = (props) => {
    const btnStyle = {
        border: props.border,
        background: props.background,
        ...props.add
    }
    return (
        <button onClick={props.clicked} className={classes.button + " " + classes[props.type]} style={btnStyle}>
            {props.download ? <a href={props.to} style={{color: props.color}} download="Nicholas Yap's CV">{props.text}</a>:
            <Link to={props.to} style={{color: props.color}}>{props.text}</Link>}
        </button>
    )
}

export default button