import React, { useEffect } from 'react'
import Layout from '../../components/Layout/Layout'
import Button from '../../components/UI/Button/Button'
import classes from './HomePage.module.css'
import effects from '../../effects/effects'
import { IoMailSharp, IoLogoLinkedin, IoLogoGithub } from 'react-icons/io5'

const HomePage = () => {
    useEffect(() => {
        const text = "Hello, nice to meet you. My name is Nicholas Yap. I am a software developer based in Singapore."
        const introduction = document.getElementById("intro-text")
        const cursor = document.getElementById("cursor");
        effects.typewriter(introduction, text, 0)
        effects.blinkingCursor(cursor, text, 0)
    }, [])
    return (
        <Layout>
            <div className={classes.page}>
                <div className={classes.content}>
                    <h1>Nicholas Yap</h1>
                    <div className={classes.introTextContainer}>
                        <div>
                            <div id="intro-text" style={{ display: "inline" }}></div>
                            <span id="cursor" style={{ display: "inline" }}>|</span>
                        </div>
                    </div>
                    <div className={classes.iconsContainer}>
                        <a href="mailto:nicyzk@gmail.com" style={{color:"orange"}}><IoMailSharp/></a>
                        <a href="https://www.linkedin.com/" style={{color: "#2867B2"}}><IoLogoLinkedin/></a>
                        <a href="https://github.com/Nicyzk" style={{color: "purple"}}><IoLogoGithub/></a>
                    </div>
                    <div className={classes.btnContainer}>
                        <Button
                            border="none"
                            color="white"
                            text="Projects"
                            background="salmon"
                            type="intro"
                            to="/projects"
                            download={false}
                        />
                        <Button
                            border="2px solid salmon"
                            color="salmon"
                            text="Get CV"
                            background="white"
                            type="intro"
                            to={process.env.PUBLIC_URL + "/files/CV.pdf"}
                            download={true} />
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default HomePage

