import React from 'react'
import classes from './Loading.module.css'
import Backdrop from '../Backdrop/Backdrop'

const loading = () => {
    return (
        <Backdrop>
            <div className={classes.container}>
                <span className={classes.arw1}>↓</span>
                <span className={classes.arw2} >↓</span>
                <span className={classes.arw3} >↓</span>
                <span className={classes.arw4} >↓</span>
                <span className={classes.arw5} >↓</span>
            </div>
        </Backdrop>
    )
}

export default loading