import React from 'react'
import Layout from '../../components/Layout/Layout'
import classes from './AboutPage.module.css'
import Experience from './Experience/Experience'

const AboutPage = () => {
    return (
        <Layout>
            <div className={classes.page}>
                <div className={classes.content}>
                    <div>
                        <h1>About</h1>
                        <p>I'm a student currently enrolled in NTU's Renaissance Engineering Program. I'm interested in software engineering
                        and building products that will influence and improve the lives of others. I am proficient in web technologies
                        and have experience building web applications. I invite you to check out my github page. If you like what you
                        see, please do reach out!
                        </p>
                    </div>
                    <div>
                        <h1>Experience</h1>
                        <Experience
                        position="Sarment Holdings (Accounts Intern)"
                        duration="Feb 2019 - Mar 2019"
                        points={[
                            "Assisted Accounts manager in year-end consolidation of financial statements"
                        ]}
                        />
                        <Experience 
                        position="Whizpace Pte Ltd (Software Intern)"
                        duration="Jan 2019"
                        points={[
                            "Helped to perform testing for WhizMesh product",
                            "Used Bash and PHP to improve GUI of product"
                        ]}
                        />
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default AboutPage