import React, {useState} from 'react'
import classes from './Header.module.css'
import { Link } from 'react-router-dom'

const Header = () => {
    const [menuIconClicked, setMenuIconClicked] = useState(false)

    const onMenuIconClicked = () => {
        setMenuIconClicked(!menuIconClicked)
    }
    const menuSliderStyle = menuIconClicked ? {transform: "translateX(0%)"}: {transform: "translateX(100%)"}
    //window.addEventListener('resize', ...)

    return (
        <>
            <header className={classes.navbar}>
                <div className={classes.logo}><Link to="/">Nicholas Yap</Link></div>
                <nav>
                    <span 
                    className={menuIconClicked ? classes.clicked : null}
                    onClick={onMenuIconClicked}></span>
                </nav>
            </header>
            <div className={classes.menuSlider} style={menuSliderStyle}>
                <ul>
                    <li><Link to="/">Home</Link></li>
                    <li><Link to="/about">About</Link></li>
                    <li><Link to="/projects">Projects</Link></li>
                    <li><Link to="/contact">Contact</Link></li>
                </ul>
            </div>
        </>
    )
}

export default Header