
// typewriter effect 
const typewriter = (introduction, text, i) => {
    if (i < text.length) {
        introduction.innerHTML += text.charAt(i);
        i++
        setTimeout(() => typewriter(introduction, text, i), 50)
    }
}

const blinkingCursor = (cursor, text, i) => {
    if (window.getComputedStyle(cursor).getPropertyValue('visibility') === "visible") {
        cursor.style.visibility = "hidden"
    } else {
        cursor.style.visibility = "visible"
    }
    if (i < (text.length / 3)) {
        i++
        setTimeout(() => blinkingCursor(cursor, text, i), 250)
    }
}

const effects = {typewriter, blinkingCursor}

export default effects