import React from 'react'
import Layout from '../../components/Layout/Layout'
import classes from './ProjectsPage.module.css'
import Card from '../../components/UI/Card/Card'

const ProjectsPage = () => {
    return (
        <Layout>
            <div className={classes.page}>
                <div className={classes.content}>
                    <h1>Projects</h1>
                    <div className={classes.cardsContainer}>
                        <Card 
                        src={process.env.PUBLIC_URL + '/img/e-commerce-website.png'} 
                        alt="E-commerce Website"
                        title="Full-stack MERN E-commerce App"
                        github="https://github.com/Nicyzk/e-commerce-front-end"/>
                        <Card 
                        src={process.env.PUBLIC_URL + '/img/personal-portfolio.png'} 
                        alt="Personal Portfolio"
                        title="Personal Portfolio"
                        github="https://github.com/Nicyzk/personal-portfolio"/>
                        <Card 
                        src={process.env.PUBLIC_URL + '/img/sudoku-solver.png'} 
                        alt="Sudoku Solver"
                        title="Sudoku Solver"
                        github="https://github.com/Nicyzk/sudoku-solver"/>
                        <Card 
                        src={process.env.PUBLIC_URL + '/img/budget-app.png'} 
                        alt="Budget App"
                        title="Budget App"
                        github="https://github.com/Nicyzk/simple-budget-app"/>
                        <Card 
                        src={process.env.PUBLIC_URL + '/img/random-quote-generator.png'} 
                        alt="Random Quote Generator"
                        title="Random Quote Generator"
                        github="https://github.com/Nicyzk/random-quote-machine"/>
                        <Card 
                        src={process.env.PUBLIC_URL + '/img/drum-machine.png'} 
                        alt="Drum Machine"
                        title="Drum Machine"
                        github="https://github.com/Nicyzk/drum-machine"/>
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default ProjectsPage