import classes from './ContactPage.module.css'
import React, {useState} from 'react'
import Layout from '../../components/Layout/Layout'
import Button from '../../components/UI/Button/Button'
import FormGroup from '../../components/UI/FormGroup/FormGroup' 
import Modal from '../../components/UI/Modal/Modal'
import axios from 'axios'
import Loading from '../../components/UI/Loading/Loading'

const ContactPage = () => {
    const [show, setShow] = useState(false)
    const [modalDetails, setModalDetails] = useState({
        statusCode: 0,
        message: ""
    })
    const [loading, setLoading] = useState(false)
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [subject, setSubject] = useState('')
    const [description, setDescription] = useState('')

    const onSubmit = () => {
        const data = {name, email, subject, description}
        setLoading(true)

        axios.post('/message/sendmessage', data)
        .then((res) => {
            setLoading(false)
            setShow(true)
            setName('')
            setEmail('')
            setSubject('')
            setDescription('')
            setModalDetails({
                status: "Success",
                message: "Message has been sent. I will get back to you shortly :)"
            })
        })
        .catch((err) => {
            setLoading(false)
            setShow(true)
            if (err.response.status === 422 || err.response.status === 500) {
                setModalDetails({
                    status: "Error",
                    message: err.response.data.errorMessage
                })
            } else {
                setModalDetails({
                    status: "Error",
                    message: "Client-side error. Please refresh the page and try again."
                })
            }
        })
    }

    return (
        <Layout>
            {show ? <Modal 
            onClose={() => setShow(false)}
            details={modalDetails}
            /> : null}
            {loading ? <Loading/>: null}
            <div className={classes.page}>
                <div className={classes.content}>
                    <div className={classes.textContainer}>
                        <h1>Got a project?</h1>
                        <p>Hey there! I'm always open to new ideas and opportunities! If you have any thing you want to discuss about
                        please feel free to reach out and connect with me!
                        </p>
                    </div>
                    <div className={classes.form}>
                        <div className={classes.formHeader}><h1>Contact</h1></div>
                        <FormGroup value={name} changed={e => setName(e.target.value)} label="Name" id="name" />
                        <FormGroup value={email} changed={e => setEmail(e.target.value)} label="Email" id="email" />
                        <FormGroup value={subject} changed={e => setSubject(e.target.value)} label="Subject" id="subject" />
                        <FormGroup value={description} changed={e => setDescription(e.target.value)} label="Description" id="description" type="textarea" />
                        <Button
                            border="none"
                            color="white"
                            text="Send Message"
                            background="salmon"
                            type="contact"
                            to="#"
                            download={false}
                            clicked={onSubmit}
                        />
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default ContactPage